import {
	getRasume,
	upRasumePubdate,
} from '@/api/job'
import {
	$wuxToast,
} from '../../wxcomponents/wux/index'
import {
	mapGetters
} from 'vuex'
export default {
	computed: {
		...mapGetters([
			'member',
		])
	},
	data() {
		return {
			detail: {
				info: {
					type: 0
				}
			},
		}
	},
	onShow() {
		uni.hideKeyboard()
		this.$store.commit('SET_LTABBAR', 'resume');
		this.getRasume()
		if (this.$store.getters.member.ac < 2) {
			this.$store.dispatch('MemberAC')
		}
	},
	onLoad() {
		uni.hideTabBar()
	},
	methods: {
		getRasume() {
			getRasume().then(response => {
				let r = response.data
				if (response.code === 20000) {
					this.detail = r.detail
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '请求异常'
					})
				}
			})
		},
		upRasumePubdate() {
			upRasumePubdate().then(response => {
				let r = response.data
				if (response.code === 20000) {
					if (r.up) {
						$wuxToast().show({
							type: 'success',
							text: '刷新成功'
						})
					}
				} else {
					$wuxToast().show({
						type: 'forbidden',
						text: '刷新失败'
					})
				}
			})
		},
		basicTo() {
			uni.navigateTo({
				url: '/pages/resume/basic'
			});
		},
		prefTo() {
			uni.navigateTo({
				url: '/pages/resume/pref'
			});
		},
		settingTo() {
			uni.navigateTo({
				url: '/pages/resume/setting'
			});
		},
		previewTo() {
			uni.navigateTo({
				url: '/pages/resume/preview'
			});
		},
		projectTo() {
			if (this.detail.project.length > 0) {
				uni.navigateTo({
					url: '/pages/resume/project/index'
				});
			} else {
				uni.navigateTo({
					url: '/pages/resume/project/add'
				});
			}
		},
		eduTo() {
			if (this.detail.edu.length > 0) {
				uni.navigateTo({
					url: '/pages/resume/edu/index'
				});
			} else {
				uni.navigateTo({
					url: '/pages/resume/edu/add'
				});
			}
		},
		honorTo() {
			if (this.detail.honor.length > 0) {
				uni.navigateTo({
					url: '/pages/resume/honor/index'
				});
			} else {
				uni.navigateTo({
					url: '/pages/resume/honor/add'
				});
			}
		},
		workTo() {
			if (this.detail.work.length > 0) {
				uni.navigateTo({
					url: '/pages/resume/work/index'
				});
			} else {
				uni.navigateTo({
					url: '/pages/resume/work/add'
				});
			}
		}
	}
}
